}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

body {
  font-family: var(--font-family, sans-serif);
  background: transparent;
}

.page {
  height: 100%;
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container-fluid {
  margin: 0 auto;
  max-width: var(--content-fluid-width);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.header {
  position: fixed;
  top: 0;
  z-index: 2;
  width: 100%;
  max-width: 1920px;
  background: var(--header-bg-color);
}
.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100px;
}
.header .nav__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 60px;
}
.header .nav__link {
  font-weight: 600;
  font-size: var(--small-size);
  line-height: normal;
  text-transform: uppercase;
  color: var(--light-color);
  transition: all ease-in-out 0.3s;
}
.header .nav__link:hover {
  color: var(--similar-color);
}

.burger {
  --burger-width: 30px;
  --burger-height: 30px;
  --burger-line-height: 2px;
  position: relative;
  border: none;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  color: #000;
  background-color: transparent;
  cursor: pointer;
  display: none;
}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger::before {
  top: 0;
}
.burger::after {
  top: calc(100% - var(--burger-line-height));
}
.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transform: translateY(-50%);
  transition: transform 0.3s ease-in-out;
}
.burger--active::before {
  top: 50%;
  transform: rotate(45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active::after {
  top: 50%;
  transform: rotate(-45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}

.burger__line {
  color: var(--light-color);
}

.burger::before,
.burger::after {
  color: var(--light-color);
}

.about {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 205px 0 203px;
  background-image: url(./../img/top-bg.png);
}
.about__inner {
  max-width: 780px;
}
.about__title {
  font-weight: 700;
  font-size: var(--main-title-size);
  line-height: normal;
  text-transform: uppercase;
  color: var(--accent-color);
}
.about__title span {
  color: var(--black-color);
}
.about__list-title {
  margin-top: 90px;
  font-weight: 700;
  font-size: var(--default-size);
  line-height: normal;
  color: var(--black-color);
}
.about__list {
  margin-top: 10px;
  padding-left: 25px;
}
.about__list-item {
  position: relative;
  font-weight: 400;
  font-size: var(--default-size);
  color: var(--black-color);
}
.about__list-item::before {
  content: "";
  position: absolute;
  left: -25px;
  top: 0;
  width: 25px;
  height: 22px;
  background-image: url(./../img/dash.png);
}
.about__list-item + .about__list-item {
  margin-top: 5px;
}
.about__btns {
  display: flex;
  column-gap: 30px;
  margin-top: 80px;
}
.about__btn-order, .about__btn-more {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  padding: 14px 10px;
  width: 100%;
  max-width: 183px;
  font-weight: 400;
  font-size: var(--small-size);
  text-align: center;
  text-transform: uppercase;
  transition: all ease-in-out 0.3s;
}
.about__btn-order {
  border: 1px solid var(--light-color);
  color: var(--light-color);
  background: var(--accent-color);
}
.about__btn-more {
  border: 1px solid #6f6d7b;
  color: var(--btn-hover-color);
  background: var(--light-color);
}
.about__btn-order:hover {
  border: 1px solid #6f6d7b;
  color: var(--btn-hover-color);
  background: var(--light-color);
}
.about__btn-more:hover {
