}
.order__item {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 209px;
}
.order__item picture {
  position: relative;
  margin: 0 auto;
}
.order__item picture::before {
  content: "";
  position: absolute;
  left: calc(-100% - 40px);
  top: 50%;
  width: 111px;
  height: 6px;
  background-image: url(./../img/dots.png);
