}
.footer__line {
  position: relative;
}
.footer__line::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  width: 1px;
  height: 18px;
  background: var(--footer-color);
  transform: translateY(-50%);
}
.footer .payment__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 2vw;
  font-weight: 400;
  font-size: var(--small-size);
  color: var(--footer-color);
}
.footer .payment__item {
  display: flex;
  align-items: center;
}
.footer .payment__item::before {
